import DashboardPage from "./pages/Dashboard/DashboardPage";
import SubscriptionsPage from "./pages/Subscriptions/SubscriptionsPage";

const routes = [
  {
    id: '0',
    route_id: '0',
    path: "dashboard",
    element: <DashboardPage />
  },
  {
    id: '1',
    route_id: '1',
    path: "subscriptions",
    children: [
      {
        id: 'A',
        route_id: '1-A',
        index: true,
        element: <SubscriptionsPage />
      },
      {
        id: 'B',
        route_id: '1-B',
        path: ":id",
        element: <SubscriptionsPage />
      },
    ]
  },
]

export default routes;