import "./Ad.scss";

const Ad = ({ ad_image }) => {
  return (
    <div className="ad-item" style={{ backgroundImage:`url(${ad_image})` }}>
      <div className="ad-item-overlay" />
      <div className="ad-item-content">
        <div>
          <span className="font-semibold text-3xl text-white">Lorem Ipsum</span>
        </div>
        <div>
          <span className="text-lg sm:text-2xl text-white leading-none">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</span>
        </div>
        <div className="absolute right-4 bottom-4 flex items-center gap-2 px-3 sm:px-4 py-1 sm:py-2 rounded-full bg-primary brightness-100 z-1">
          <span className="font-semibold text-white text-sm sm:text-base">Learn More</span>
          <span className="font-semibold text-white text-sm sm:text-base"><i className="fa-solid fa-arrow-right-long fa-xs" /></span>
        </div>
      </div>
    </div>
  )
}

export default Ad;