import { memo } from "react";

const Button = memo(function Button({
  children,
  type = "button",
  primary,
  primaryReversed,
  full,
  disabled,
  roundedFull,
  className,
}) {
  const customClassName = [
    "zui-button",
    primary ? "zui-button--primary" : "",
    primaryReversed ? "zui-button--primary-reversed" : "",
    full ? "w-full" : "",
    roundedFull ? "rounded-full" : "rounded-lg",
    className,
  ].join(" ");

  return (
    <button
      type={type}
      className={customClassName}
      disabled={disabled}
    >
      {children}
    </button>
  );
});

export default Button;
