import { StrictMode, useEffect, useState } from 'react';
import ReactDOM from 'react-dom/client';
import './index.scss';
import routes from './routes';
import LoginPage from './pages/Login/LoginPage';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import MainLayout from './assets/layouts/Main/MainLayout';
import NotFoundPage from './pages/NotFound/NotFoundPage';

const ipLocations = [
  "ph", "sg", "my", "th"
]

const App = () => {
  // DEV
  // const [ipLocation, setIPLocation] = useState((ipLocations.indexOf(document.location.pathname.split("/")[1]) > -1) ? ipLocations[ipLocations.indexOf(document.location.pathname.split("/")[1])] : "ph")
  
  // PROD
  const [ipLocation, setIPLocation] = useState((ipLocations.indexOf(document.location.pathname.split("/")[2]) > -1) ? ipLocations[ipLocations.indexOf(document.location.pathname.split("/")[2])] : "ph")

  const mapRoutes = (routes) => {
    return routes.map((route, index) => {
      return (
        <Route key={ index } exact index={route.index ?? false} path={route.path} element={route.element}>
          {route.children && mapRoutes(route.children)}
        </Route>
      )
    })
  }

  return (
    <BrowserRouter basename={`/customer-portal-react/${ipLocation}`}>
      <Routes>
        <Route path="/" element={ <LoginPage ipLocations={ipLocations} ipLocation={ipLocation} setIPLocation={setIPLocation} /> }  />
        <Route path={ipLocation} element={ <LoginPage ipLocations={ipLocations} ipLocation={ipLocation} setIPLocation={setIPLocation} /> }  />
        <Route path={ipLocation} element={ <MainLayout /> }>
          { mapRoutes(routes) }
        </Route>
        <Route path="*" element={ <NotFoundPage /> } />
      </Routes>
    </BrowserRouter>
  )
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <StrictMode>
    <App />
    <ToastContainer hideProgressBar={ true } />
  </StrictMode>
);