import "./MainLayout.scss";
import { Link, Outlet, useParams } from "react-router-dom";
import ZUI from "../../../components/zui/zui";
import logo from "../../images/logo.svg";
import Button from "../../../components/zui/Button/Button";
import { Fragment, useState } from "react";

const menuItems = [
  {
    name: "Subscriptions",
    url: "subscriptions",
    icon: <i className="fa-solid fa-box" />,
  },
  {
    name: "Bills & Payments",
    icon: <i className="fa-solid fa-receipt" />,
    children: [
      {
        name: "Payment Options",
        url: "payment-options",
        icon: <i className="fa-solid fa-credit-card fa-sm" />,
      },
      {
        name: "Invoices & Payment",
        url: "invoices-and-payment",
        icon: <i className="fa-solid fa-file-invoice-dollar" />,
      },
    ]
  },
  {
    name: "Support",
    icon: <i className="fa-regular fa-message" />,
    children: [
      {
        name: "Tickets",
        url: "tickets",
        icon: <i className="fa-solid fa-ticket" />,
      },
    ]
  },
]

const MainLayout = () => {
  // const [ipLocation] = useState(document.location.pathname.split("/")[1])
  const [ipLocation] = useState(document.location.pathname.split("/")[2])

  const mapMenuItems = (items) => {
    return items?.map((item, index) => {
      if (item.url) {
        return (
          <Link key={index} className="nav-item flex gap-2" to={`/${ipLocation}/${item.url}`}>
            <div className="w-3 text-right">{item.icon}</div>
            <div>{item.name}</div>
          </Link>
        )
      } else {
        return (
          <Fragment key={ index }>
            <button className="nav-item flex gap-2">
              <div className="w-3 text-right">{item.icon}</div>
              <div>{item.name}</div>
              <div className="ml-2 mt-0.5"><i className="fa-solid fa-chevron-right fa-xs" /></div>
              <div className="nav-submenu">
                {mapMenuItems(item.children)}
              </div>
            </button>
          </Fragment>
        )
      }
    })
  }

  return (
    <div className="main-layout flex flex-col w-full h-full">
      <header className="flex justify-center w-full py-8">
        <div className="w-full h-[360px] bg-black absolute top-0 left-0 z-[-1]" />
        <div className="w-full max-w-7xl px-4">
          <div className="flex items-center">
            <div className="max-w-[120px]">
              <Link to={`/${ipLocation}/dashboard`}>
                <img className="invert" src={ logo } alt="logo" />
              </Link>
            </div>
            <div className="nav-container hidden md:flex gap-8 lg:gap-12 ml-8 mr-8 lg:ml-16 lg:mr-16">
              {mapMenuItems(menuItems)}
            </div>
            <div className="flex gap-2 ml-auto">
              <ZUI.Button primaryReversed>
                <span><i className="fa-solid fa-user" /></span>
              </ZUI.Button>
              <ZUI.Button primaryReversed>
                <span><i className="fa-solid fa-right-from-bracket" /></span>
              </ZUI.Button>
            </div>
          </div>
        </div>
      </header>
      <main>
        <Outlet />
      </main>
      <footer className="pt-16 pb-8 mt-auto">
        <div className="text-center">
          <span className="text-primary-500">© { new Date().getFullYear() } ZGI. All Rights Reserved.</span>
        </div>
      </footer>
    </div>
  )
}

export default MainLayout;