import { memo, useState } from "react";

const Input = memo(function Input({
  autoComplete = "off",
  disabled = false,
  id,
  label,
  onChange,
  required = false,
  type = "text",
  uppercase = false,
  value,
}) {
  const [errorInput, setErrorInput] = useState(false);

  const className = `zui-input ${uppercase ? 'uppercase' : ''} ${errorInput ? 'zui-input--error' : ''}`;

  const handleInput = () => setErrorInput(false);
  const handleInvalid = () => setErrorInput(true);

  const labelClassName = `zui-input-label ${errorInput ? 'zui-input-label--error' : ''}`;

  return (
    <div className="zui-input-container">
      <input
        autoComplete={autoComplete}
        className={className}
        disabled={disabled}
        id={id}
        onChange={onChange}
        onInput={handleInput}
        onInvalid={handleInvalid}
        placeholder=" "
        required={required}
        type={type}
        value={value}
      />
      <label className={labelClassName} htmlFor={id}>
        {errorInput ? 'Error' : label}
      </label>
    </div>
  );
});

export default Input;
