import { useState } from "react";
import ZUI from "../../../components/zui/zui";
import { Tooltip } from "react-tooltip";
import notify from "../../../assets/utils/notification";
import { useNavigate } from "react-router-dom";

const LoginForm = ({ className }) => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    "account-number": 'T00001234',
    password: 'password',
  })
  const [loginPending, setLoginPending] = useState(false);

  const handleLogin = (e) => {
    e.preventDefault();
    setLoginPending(true);
    setTimeout(() => {
      notify.success('Welcome!', {
        onOpen: () => navigate(`dashboard`)
      })
    }, 1234)
  }

  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData({ ...formData, [id]: value })
  }

  return (
    <form className={["w-full max-w-xs p-6 rounded-2xl bg-white", className].join(" ")} onSubmit={ handleLogin }>
      <div className="mb-4 relative">
        <ZUI.Input type="text" id="account-number" label="Account Number" value={ formData["account-number"] } onChange={ handleChange } uppercase required disabled={ loginPending } />
        <div id="account-number-tooltip" className="absolute right-4 top-1/4 z-50">
          <span className="opacity-50"><i className="fa-solid fa-circle-info"></i></span>
        </div>
        <Tooltip anchorSelect="#account-number-tooltip" place="bottom" content="Lorem ipsum dolor sit amet" />
      </div>
      <div className="mb-4">
        <ZUI.Input type="password" id="password" label="Password" value={ formData.password } onChange={ handleChange } required disabled={ loginPending } />
      </div>
      <div>
        <ZUI.Button type="submit" primary full disabled={ loginPending }>
          <span className="relative">
          { loginPending ?
            <i className="fa-solid fa-spinner animate-spin"></i> :
            <>
              <span className="absolute left-[-24px]">
                <i className="fa-solid fa-right-to-bracket" />
              </span>
              <span>Login</span>
            </>
          }
          </span>
        </ZUI.Button>
      </div>
    </form>
  )
}

export default LoginForm;